.ModalWrapper {
  position: relative;
  display: flex;
  min-height: 94%;
  overflow-y: auto;
  width: 1007px;
  height: 650px;
  box-shadow: 0 5px 16px rgba(0, 0, 0, 0.2);
  background: #fff;
  color: #000;
  grid-template-columns: 1fr 1fr;
  z-index: 10;
  border-radius: 10px;
}

.Modal__Fullscreen {
  height: 94%;
  margin: 0;
  max-width: 100%;
  max-height: none;
  position: relative;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

