.ModalSection1 {
  display: flex;
  flex-direction: column;
  position: relative;
  justify-content: center;
  border-radius: 10px 0 0 10px;
  line-height: 1.8;
  width: 35%;
  color: #141414;
  background-color: #eff2f6;
}
.ModalBottomImg {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  border-radius: 10px 0 0 10px;
}
.mobile {
  display: none;
}

.ModalSection2 {
  background-color: white;
  width: 60%;
  flex-grow: 1;
  padding: 1.5rem 5rem;
  position: relative;
  overflow-y: auto;
}

.modal__content1 {
  padding: 2.5rem 2.5rem 0;
}
.modal__content2 {
  padding: 2.5rem 0;
}

.modal__welcome {
  font-size: 1.5rem;
  line-height: 1.875rem;
  font-weight: 600;
  margin: 24px 0;
}
.modal__signin {
  margin-top: 0.7rem;
  color: #788699;
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 400;
}

.google__login {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  border: 1px solid #1a73e8;
  font-size: 0.875rem;
  line-height: 1.25rem;
  padding: 0.625rem 0.75rem;
  cursor: pointer;
  border-radius: 3px;
  margin-top: 3%;
  color: #1a73e8;
}
.google__login > img {
  height: 15px;
  margin-right: 10px;
}
.google__login > p {
  padding: 0%;
  margin: 0;
}
.modal__seperator {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1.1625rem 0 0.9375rem;
  color: #141820;
}
.modal__seperator > hr {
  width: 100%;
  height: 1px;
  background-color: #dfe3eb;
  border: none;
  margin-block-start: 0.5em;
  margin-block-end: 0.5em;
  margin-inline-start: auto;
  margin-inline-end: auto;
  overflow: hidden;
}
.modal__seperator > p {
  margin-left: 16px;
  margin-right: 16px;
}
hr {
  display: block;
  unicode-bidi: isolate;

  border-style: inset;
  border-width: 1px;
}

.home_modal {
}
form {
  display: block;
  margin-top: 0em;
}
.modal__form > label {
  font-size: 0.875rem;
  color: #201e27;
  margin: 0.625rem 0 2%;
  font-weight: 600;
  display: block;
  line-height: 1.6;
  max-width: 42em;
}
.modal__form > input {
  border-radius: 0.3125rem;
  margin-bottom: 0.6rem;
  width: 96%;
  font-size: 0.875rem;
  font-family: Inter;
  padding: 10px;
  outline: none;
  caret-color: #48ccac;
  border: 1px solid #ddd;
  transition: all 0.3s ease-in;
}
.modal__form > input:focus {
  border: 2px solid #48ccac;
}
.modal__errors {
  color: #e0133e;
  width: 100%;
  margin-top: 0.01em;
  font-weight: 700;
  font-size: 0.8rem;
  display: flex;
  align-items: center;
}
.modal__errors > .MuiSvgIcon-root {
  color: #e0133e !important;
  font-size: 1.1rem !important;
}
.MuiSvgIcon-root {
  fill: currentColor;
  width: 1em;
  height: 1em;
  display: inline-block;
  font-size: 1.5rem;
  transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  flex-shrink: 0;
  user-select: none;
  margin-right: 4px;
}
.visiblity {
  position: relative;
  top: -2.6rem;
  right: -22rem;
  cursor: pointer;
}

.modal .home__login {
  margin-top: 1rem;
  min-width: 9rem;
  min-height: auto;
  font-size: 0.875rem;
}

.modal__form > button {
  margin: 1rem auto 3rem;
  width: 100%;
  color: #fff;
  background: #1a73e8;
  padding: 0.625rem 0.75rem;
  cursor: pointer;
  border-radius: 5px;
  max-width: 130px;
  outline: none;
  border: none;
  display: block;
  text-align: center;
  transition: all 0.2s ease-in;
}
.modal__form > button:disabled {
  opacity: 0.5 !important;
  cursor: default;
}
.home__login {
  min-width: 10rem;
  min-height: 3rem;
  border-radius: 4px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  width: 10%;
  outline: none;
  cursor: pointer;
  margin-bottom: 0;
  font-size: 1rem;
  line-height: 1.375rem;
  font-weight: 700;
  border: 1px solid #d6dae0;
  color: #201e27;
}

.modal__contentbottom {
  margin: 0 10%;
}
.modal__contentbottom > hr {
  background: #ebedf1;
  border: 0;
  display: block;
  margin: 1.25rem auto;
  height: 1px;
  width: 100%;
}
hr {
  display: block;
  unicode-bidi: isolate;
  margin-block-start: 0.5em;
  margin-block-end: 0.5em;
  margin-inline-start: auto;
  margin-inline-end: auto;
  overflow: hidden;
  border-style: inset;
  border-width: 1px;
}
.modal__contentbottom > p {
  color: #788699;
  margin-top: 1.25rem;
  margin-bottom: 1rem;
  text-align: center;
}

p {
  -webkit-margin-before: 1em;
  margin-block-start: 1em;
  -webkit-margin-after: 1em;
  margin-block-end: 1em;
  -webkit-margin-start: 0;
  margin-inline-start: 0;
  -webkit-margin-end: 0;
  margin-inline-end: 0;
}
.google__login,
.modal__contentbottom > p > span {
  color: #1a73e8;
  font-weight: 400;
}
@media (max-width: 770px) {
  .ModalSection2 {
    height: fit-content;
    width: 90%;
    margin: 20px auto;
    padding: 20px;
  }
}

@media (max-width: 415px) {
  .ModalWrapper {
    height: 100%;
    border-radius: 0%;
    margin: 0%;
    display: block;
    position: relative;
    /* flex-direction: column; */
    overflow-y: auto;
  }
  .CloseModalButton {
    position: fixed !important;
    color: grey;
  }
  .ModalSection1,
  .ModalSection2 {
    width: 100%;
    margin: 0 auto;
    padding: 0px;
    color: black;
    border-radius: 0 0 0 0;
    position: relative;
  }
  .modal__welcome {
    font-size: 20px;
  }

  .modal__content2 {
    width: 100%;
    padding: 0 27.2px;
    margin: 0;
  }
  .modal__content1 {
    z-index: 3;
    padding: 0;
  }
  .modal__content1 > p {
    text-align: center;
  }
  .ModalBottomImg {
    width: 100%;
    z-index: 0;
    border-radius: 0;
    top: 0;
    right: 0;
  }
  .mobile {
    display: block;
  }
  .big {
    display: none;
  }
  .home__login > button {
    font-size: 14px;
    margin: 16px 96.4px 48px;
  }
}
@media (max-width: 415px) {
  .ModalSection2 {
    min-height: 0%;
    height: 100%;
    overflow-y: unset;
  }
  .ModalSection1 {
    padding-top: 30px;
    height: 25%;
    background-color: white;
  }
}
